@keyframes pulsate {
  0%,
  50%,
  100% {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0.2;
  }
}
@keyframes horizontal {
  0% {
    background-size: 100% 100%;
  }
  100% {
    background-size: 0% 100%;
  }
}
@keyframes pulsing-border-dark {
  0%,
  100% {
    box-shadow: 0 0 3px #bca98a;
  }
  50% {
    box-shadow: 0 0 7px #cfc36e;
  }
}
@keyframes pulsing-border-light {
  0%,
  100% {
    box-shadow: 0 0 3px #f7dbb4;
  }
  50% {
    box-shadow: 0 0 7px #cfc36e;
  }
}
@keyframes pulsing-border-classic {
  0%,
  100% {
    box-shadow: 0 0 3px #e9d6ff;
  }
  50% {
    box-shadow: 0 0 7px #f7f8f3;
  }
}
::-webkit-scrollbar {
  display: none;
}
